const CAMPAIGN_TYPES = ['Task', 'Contest', 'Survey', 'Watch Ad', 'Campaign'];

const rows = [...Array(100)].map((_, index) => {
  const setIndex = index + 1;
  return {
    id: `id ${setIndex}`,
    name: faker.commerce.productName(),
    brandStatus: faker.datatype.number({ min: 1, max: 5, precision: 1 }),
    campaignType: faker.helpers.randomize(CAMPAIGN_TYPES),
    campaignBudget: faker.datatype.number({ min: 1000, max: 500000, precision: 1 }),
    budgetLeft: faker.datatype.number({ min: 1000, max: 500000, precision: 1 }),
    tokensGiven: faker.datatype.number({ min: 100, max: 10000, precision: 1 }),
    starsGiven: faker.datatype.number({ min: 100, max: 1000, precision: 1 }),
    additionalTokensAdded: faker.datatype.number({ min: 100, max: 5000, precision: 1 }),
    engagementRate: faker.datatype.number({ min: 100, max: 5000, precision: 1 }),
    reach: faker.datatype.number({ min: 100, max: 5000, precision: 1 }),
    targetReach: faker.datatype.number({ min: 100, max: 5000, precision: 1 }),
    status: sample(['online', 'away', 'busy']),
  };
});

// ----------------------------------------------------------------------

function CustomPagination() {
  const { state, apiRef } = useGridSlotComponentProps();

  return (
    <Pagination
      color="primary"
      count={state.pagination.pageCount}
      page={state.pagination.page + 1}
      onChange={(event, value) => apiRef.current.setPage(value - 1)}
    />
  );
}

RatingInputValue.propTypes = {
  applyValue: PropTypes.func,
  item: PropTypes.shape({
    value: PropTypes.number,
  }),
};

function RatingInputValue({ item, applyValue }) {
  return (
    <Box sx={{ pl: 2, height: 1, alignItems: 'center', display: 'inline-flex' }}>
      <Rating
        size="small"
        name="custom-rating-filter-operator"
        placeholder="Filter value"
        value={Number(item.value)}
        onChange={(event, newValue) => {
          applyValue({ ...item, value: newValue });
        }}
        precision={0.5}
      />
    </Box>
  );
}

export default function CampaignListTable() {
  if (columns.length > 0) {
    const ratingColumn = columns.find((column) => column.field === 'rating');
    const ratingColIndex = columns.findIndex((col) => col.field === 'rating');

    const ratingFilterOperators = getGridNumericColumnOperators().map((operator) => ({
      ...operator,
      InputComponent: RatingInputValue,
    }));

    columns[ratingColIndex] = {
      ...ratingColumn,
      filterOperators: ratingFilterOperators,
    };
  }

  return (
    <DataGrid
      rows={rows}
      columns={columns}
      pagination
      pageSize={10}
      components={{
        Toolbar: GridToolbar,
        Pagination: CustomPagination,
      }}
      filterModel={{
        items: [{ columnField: 'rating', value: '1.5', operatorValue: '>=' }],
      }}
    />
  );
}
